
























import Vue from 'vue';
import tagList from '@/service/get-tag-list.ts';

export default Vue.extend({
  data: () => ({
    chips: [] as string[],
    items: [] as string[],
  }),
  methods: {
    remove(item: string) {
      this.chips.splice(this.chips.indexOf(item), 1);
    },
  },
  mounted() {
    this.items.push(...tagList);
  },
  watch: {
    chips() {
      this.$store.commit('setTagList', this.chips);
    },
  },
});
